* {
  font-family: 'Spoqa Han Sans', 'Spoqa Han Sans JP', 'Sans-serif';
  /* background-color: #f4f4f6; */
}
.App {
  width: 500px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
.add-button button {
  position: absolute;
  bottom: 5%;
  right: 5%;
  color: #fff;
  padding: 15px;
  font-size: 18px;
  border: none;
  background-color: #a293dd;
  cursor: pointer;
}
.diary-editor {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.diary-editor .header {
  background-color: #a293dd;
}
.diary-editor h2 {
  color: #fff;
  font-family: 'Caprasimo', cursive;
  letter-spacing: 2px;
  font-size: 40px;
}
.diary-editor p {
  color: #fff;
  font-size: 20px;
}
.diary-editor input , textarea {
  width: 500px;
  padding: 10px;
  outline: none;
  border: 1px solid #C7B9FF;
}
.diary-editor input::placeholder , textarea::placeholder {
  color: #a293dd;
}
.diary-editor textarea {
  height: 350px;
}
.diary-editor .emotion-box {
  width: 500px;
}
.diary-editor span {
  font-family: 'Spoqa Han Sans', 'Spoqa Han Sans JP', 'Sans-serif';
}
.diary-editor select {
  width: 250px;
  padding: 10px;
  outline: none;
  border: 1px solid #a293dd;
  cursor: pointer;
}
.diary-editor .emotion-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.diary-editor .emotion-box button {
  width: 100px;
  padding: 10px;
  border: none;
  background-color: #a293dd;
  color: #fff;
  cursor: pointer;
}

/* DiaryList */

.diary-list {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background: #ebe9f6;
  overflow: scroll;
}
.title {
  top: 7%;
  font-size: 40px;
  font-family: 'Caprasimo', cursive;
  letter-spacing: 2px;
  color: #a293dd;
}
.diary-list .box .close-btn {
  position: absolute;
  top: 10%;
  right: 10%;
  color: #a293dd;
  font-size: 50px;
  cursor: pointer;
}
.diary-item-content{
  padding: 20px;
  background-color: #fff;
  width: 800px;
  margin-bottom: 20px;
}
/* DiaryItem */
.diary-item .content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.diary-item svg {
  font-size: 35px;
}